@import url(https://fonts.googleapis.com/css?family=Caveat|Comfortaa|Indie+Flower|Julius+Sans+One|Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 800px;
  background-image: url(data:image/png;base64,)
}

.container {
  width: 65%;
  height: 600px;
  border: 2px solid #794044;
  border-radius: 8px;
  margin: 80px;
  background-color: #f9f7f1;
}

.under-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.welcome {
  font-family: 'Comfortaa', cursive;
  font-size: 70px;
  color: #794044;
  margin-top: 45px;
  margin-bottom: 10px; 
}

.navbar {
  border: 3px solid #794044;
  background-color: #794044;
  display: flex;
  margin: 12px 15px; 
  border-radius: 8px;
  font-family: 'Quicksand', sans-serif;
  color: #ffe4c4;
  font-weight: bold;
}

.nav-one {
  display: flex;
  justify-content: space-between;

}

.logo {
  margin: 7px 15px;
  font-size: 20px;
}
.navTwo-links {
  display: flex;
  margin-left: 420px;
}
.navbar-links{
  display: flex;
  height: 33px;
  margin-top: 3px;
  margin-right: 15px; 
}
.navbar-link {
  text-decoration: none;
  color: #ffe4c4;
  border: 2px solid #ffe4c4;
  border-radius: 10px;
  font-size: 15px;
  width: 65px;
  margin-left: 30px;
  padding-top: 3px;
  display: flex;
  justify-content: center;
}
.active {
  background-color: #ffe4c4;
  color: #794044;
}

.registration-box {
  border: 2px solid #794044;
  border-radius: 10px;
  padding: 15px;
  margin-top: 55px; 
  font-family: 'Quicksand', sans-serif;
  font-size: 18px
}
.login-box {
    border: 2px solid #794044;
    border-radius: 10px;
    width: 250px;
    height: 110px;
    margin: 80px;
    display: flex;
    justify-content: center;
    font-family: 'Quicksand', sans-serif;
    padding-top: 20px;
}

button {
    border-radius: 8px;
    font-family: inherit;
}
.home-links {
    display: flex;
    flex-direction: column;
    text-align: right;
}

a {
   font-family: 'Quicksand', sans-serif;;
    color: #794044;
}

h6 {
    color: #794044;
    font-family: 'Quicksand', sans-serif;
    font-size: 20px
}
.about-string {
    font-weight: 200;
    color: #794044;
    font-size: 20px;
    margin: 20px 0;
    font-family: 'Quicksand', sans-serif;
}
span {
    font-weight: bold;
}

.about-para {
    color: #794044;
    font-family: 'Quicksand', sans-serif;
    font-weight: 900px;
    border-radius: 8px;
    margin: auto 60px;
    padding: 10px;
    font-weight: 200;
    font-size: 12px;
    background-image: url(/static/media/clean-gray-paper.18beaf05.png);
    background-color: #ffe4c4;
}
h4 {
    color: #794044;
    font-family: 'Comfortaa', cursive;
    font-size: 30px;
    margin: 0 0 25px 0;
}
h5 {
    color: #794044;
    font-family: 'Quicksand', sans-serif;
    font-size: 20px;
    text-align: center;
    text-decoration: underline;
}

.quote-container {
    width: 400px;
    height: 300px;
    overflow: auto;
    border: 1px solid #794044;
    border-radius: 10px;

}

.quote {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.munch-box {
    padding: 10px;
}

.title {
    text-align: center;
    font-size: 20px;
    margin-bottom: 15px;
}

.munch-quote {
    text-align: left;
    font-size: 12px;
    font-weight: 200;
    margin: 20px 0;
}

.author {
    text-align: left;
    font-size: 10px;
    font-weight: 500;
    margin: 0 0 10px 0;
}

.munch-slogan {
    color: #794044;
    font-family: 'Quicksand', sans-serif;
    -webkit-text-decoration: underlines;
            text-decoration: underlines;
    margin-bottom: 0;
}

.prompt-box {
    width: 250px;
    height: 300px;
    overflow-y: scroll;
    border: 1px solid #794044; 
    border-radius: 8px;
    color: #794044;
    font-family: 'Quicksand', sans-serif;
}

.prompt {
    color: #794044;
    font-size: 12px;
    padding-left: 5px;
}

.entry-input{
    width: 250px;
    height: 300px;
    background-image: url(/static/media/clean-gray-paper.18beaf05.png);
    margin-top: -50px;
    font-family: 'Caveat', cursive;
    font-size: 18px;
}

.prompt-newEntry {
    display: flex;
}
.date {
    text-align: right;
}

.entry-button{
    border-radius: 8px;
    margin-left: 20px;
}
.entries:hover {
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
}

.user-name {
    font-family: 'Quicksand', sans-serif;
    font-size: 40px;
    color: #794044;
    margin-bottom: 15px;
}

.level {
    text-align: left;
    margin-top: 5px;
    margin-bottom: 10px;
}

.user-info {
    margin-left: 10px;
}
.react-calendar__month-view__days__day--weekend {
    color: #0fac82;
}

.react-calendar {
    border-radius: 6px;
}

.check-in {
    font-size: 15px;
    margin-top: 10px;
}

.journal {
    width: 250px;
    height: 300px;
}

.cal-jour {
    display: flex;
}

.calendar {
    margin: 10px 10px;

}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}
  
.modal-main {
      position:fixed;
      background: #f9f7f1;
      width: 80%;
      height: auto;
      top:50%;
      left:50%;
      -webkit-transform: translate(-50%,-50%);
              transform: translate(-50%,-50%);
      overflow-y: scroll;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
  
.modal-quote {
  margin-top: 15px;
  margin-bottom: 15px;
}

.entry-page {
  padding: 7px;
}

.profile-modal {
  width: 300px;
  height: 200px;
  padding: 15px;
}
